<template><section><!--
 * @Author: Fone丶峰
 * @Date: 2019-10-22 11:32:29
 * @LastEditors: Fone丶峰
 * @LastEditTime: 2020-05-12 15:41:09
 * @Description: msg
 * @Email: qinrifeng@163.com
 * @Github: https://github.com/FoneQinrf
 -->
<h3>Upload 表单校验</h3>
<hr>
<h4>使用方式</h4>
<pre v-pre><code v-pre class="language-javascript">
<span class="hljs-keyword">import</span> Vue <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { Upload } <span class="hljs-string">'vvmui'</span>

Vue.component(Upload)

</code></pre>
<h4>基础用法</h4>
<p><code v-pre>quality</code>属性可设置图片压缩的比例</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Upload</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"src"</span>
    <span class="hljs-attr">action</span>=<span class="hljs-string">"https://jsonplaceholder.typicode.com/posts/"</span>
    <span class="hljs-attr">:onSuccess</span>=<span class="hljs-string">"onSuccess"</span>
    <span class="hljs-attr">:quality</span>=<span class="hljs-string">"0.7"</span>
  /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">src</span>: <span class="hljs-string">""</span>
    };
  },
  <span class="hljs-attr">methods</span>: {
    onSuccess(file) {
      <span class="hljs-keyword">this</span>.src = URL.createObjectURL(file);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>接管文件上传</h4>
<p>使用者需要自行上传文件时可通过<code v-pre>beforeUpload</code>属性传入自定义接管文件上传方法。</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Upload</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span>
    <span class="hljs-attr">action</span>=<span class="hljs-string">"https://jsonplaceholder.typicode.com/posts/"</span>
    <span class="hljs-attr">:beforeUpload</span>=<span class="hljs-string">"beforeUpload"</span>
  /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">model</span>: {
        <span class="hljs-attr">value</span>: <span class="hljs-string">""</span>
      }
    };
  },
  <span class="hljs-attr">methods</span>: {
    beforeUpload(file) {
      <span class="hljs-keyword">this</span>.value = URL.createObjectURL(file);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>自定义占位图</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Upload</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"file"</span>
    <span class="hljs-attr">action</span>=<span class="hljs-string">"https://jsonplaceholder.typicode.com/posts/"</span>
    <span class="hljs-attr">:quality</span>=<span class="hljs-string">"0.7"</span>
    <span class="hljs-attr">:beforeUpload</span>=<span class="hljs-string">"fileBeforeUpload"</span>
  &gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"iconfile"</span>/&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">Upload</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">file</span>: <span class="hljs-string">""</span>
    };
  },
  <span class="hljs-attr">methods</span>: {
    fileBeforeUpload(file) {
      <span class="hljs-keyword">this</span>.file = URL.createObjectURL(file);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>多文件上传</h4>
<p><code v-pre>type</code>默认<code v-pre>single</code>单文件上传，设置为<code v-pre>multiple</code>启用多文件上传。</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Upload</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"array"</span>
    <span class="hljs-attr">action</span>=<span class="hljs-string">"https://jsonplaceholder.typicode.com/posts/"</span>
    <span class="hljs-attr">type</span>=<span class="hljs-string">"multiple"</span>
    <span class="hljs-attr">:beforeUpload</span>=<span class="hljs-string">"multipleBeforeUpload"</span>
  /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">array</span>: []
    };
  },
  <span class="hljs-attr">methods</span>: {
    multipleBeforeUpload(file) {
      <span class="hljs-keyword">this</span>.array.push(URL.createObjectURL(file));
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>多文件上传，限制上传个数</h4>
<p>多文件上传时可通过<code v-pre>maxNumber</code>属性控制最大上传个数。</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Upload</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"list"</span>
    <span class="hljs-attr">action</span>=<span class="hljs-string">"https://jsonplaceholder.typicode.com/posts/"</span>
    <span class="hljs-attr">type</span>=<span class="hljs-string">"multiple"</span>
    <span class="hljs-attr">:beforeUpload</span>=<span class="hljs-string">"maxNumberBeforeUpload"</span>
    <span class="hljs-attr">:maxNumber</span>=<span class="hljs-string">"3"</span>
  /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">list</span>: []
    };
  },
  <span class="hljs-attr">methods</span>: {
    maxNumberBeforeUpload(file) {
      <span class="hljs-keyword">this</span>.list.push(URL.createObjectURL(file));
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>多文件展示控制</h4>
<p><code v-pre>showRemove</code>属性可关闭文件删除图标，通过设置<code v-pre>value-string</code>属性可以更灵活的展示多文件类型的列表。</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Upload</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"files"</span>
    <span class="hljs-attr">type</span>=<span class="hljs-string">"multiple"</span>
    <span class="hljs-attr">disabled</span>
    <span class="hljs-attr">:showRemove</span>=<span class="hljs-string">"false"</span>
    <span class="hljs-attr">:maxNumber</span>=<span class="hljs-string">"4"</span>
    <span class="hljs-attr">:value-string</span>=<span class="hljs-string">"false"</span>
  /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">files</span>: [
        {
          <span class="hljs-attr">value</span>:
            <span class="hljs-string">"https://user-gold-cdn.xitu.io/2020/3/4/170a44b4cbae7897?imageView2/1/w/1304/h/734/q/85/format/webp/interlace/1"</span>,
          <span class="hljs-attr">isImage</span>: <span class="hljs-literal">true</span>
        },
        {
          <span class="hljs-attr">value</span>:
            <span class="hljs-string">"https://user-gold-cdn.xitu.io/2020/3/4/170a3678f7d5904c?imageView2/0/w/1280/h/960/format/webp/ignore-error/1"</span>,
          <span class="hljs-attr">isImage</span>: <span class="hljs-literal">true</span>
        },
        {
          <span class="hljs-attr">value</span>:
            <span class="hljs-string">"https://user-gold-cdn.xitu.io/2020/3/4/170a3678f7d5904c?imageView2/0/w/1280/h/960/format/webp/ignore-error/1"</span>,
          <span class="hljs-attr">isImage</span>: <span class="hljs-literal">false</span>
        }
      ]
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h3>API</h3>
<h5>Upload props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>绑定的值</td>
<td>String / Array</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>icon</td>
<td>占位icon</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>value-string</td>
<td>多文件类型展示的时候可设置该属性</td>
<td>Boolean</td>
<td>无</td>
<td>true</td>
</tr>
<tr>
<td>size</td>
<td>图片超过指定大小不让上传</td>
<td>Number</td>
<td>无</td>
<td>3072</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用上传</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>icon-size</td>
<td>占位icon大小</td>
<td>Number</td>
<td>无</td>
<td>24</td>
</tr>
<tr>
<td>name</td>
<td>input的原生属性</td>
<td>String</td>
<td>无</td>
<td>file</td>
</tr>
<tr>
<td>accept</td>
<td>限制文件上传的类型</td>
<td>Array</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>acceptErrorMessage</td>
<td>文件类型错误的提示内容</td>
<td>String</td>
<td>无</td>
<td>文件类型错误</td>
</tr>
<tr>
<td>compress</td>
<td>是否开启图片压缩</td>
<td>Boolean</td>
<td>无</td>
<td>true</td>
</tr>
<tr>
<td>compress-size</td>
<td>图片超过该大小进行压缩</td>
<td>Number</td>
<td>无</td>
<td>512</td>
</tr>
<tr>
<td>data</td>
<td>上传附带的内容</td>
<td>Object</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>action</td>
<td>上传地址</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>headers</td>
<td>上传的请求头部</td>
<td>Object</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>img-width</td>
<td>图片压缩时指定压缩的图片宽度，传false时会默认当前图片的宽度</td>
<td>Number / Boolean</td>
<td>无</td>
<td>800</td>
</tr>
<tr>
<td>quality</td>
<td>图片压缩的质量</td>
<td>Number</td>
<td>无</td>
<td>1</td>
</tr>
<tr>
<td>onSuccessText</td>
<td>上传成功的提示内容</td>
<td>String</td>
<td>无</td>
<td>上传成功</td>
</tr>
<tr>
<td>onErrorText</td>
<td>上传成功的提示内容</td>
<td>String</td>
<td>无</td>
<td>上传失败</td>
</tr>
<tr>
<td>showRemove</td>
<td>是否展示删除icon</td>
<td>Boolean</td>
<td>无</td>
<td>true</td>
</tr>
<tr>
<td>type</td>
<td>上传方式</td>
<td>String</td>
<td><code v-pre>single</code> <code v-pre>multiple</code></td>
<td>single</td>
</tr>
<tr>
<td>maxNumber</td>
<td>多文件上传时，最大上传的个数</td>
<td>Number</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>isImage</td>
<td>展示的文件是否为图片</td>
<td>Boolean</td>
<td>无</td>
<td>true</td>
</tr>
<tr>
<td>beforeUpload</td>
<td>上传文件之前调用的钩子，一旦传入该函数，内部上传将不会执行。</td>
<td>Function，<code v-pre>(file) =&gt; {}</code></td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>onSuccess</td>
<td>上传成功调用的钩子</td>
<td>Function，<code v-pre>(file, res) =&gt; {}</code></td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>onError</td>
<td>上传失败调用的钩子</td>
<td>Function，<code v-pre>(file, res) =&gt; {}</code></td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>onLoadend</td>
<td>文件上传成功或者失败都会调用的钩子</td>
<td>Function，<code v-pre>(file, res) =&gt; {}</code></td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>onProgress</td>
<td>文件上传进度调用的钩子</td>
<td>Function，<code v-pre>(file, res) =&gt; {}</code></td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>beforeRemove</td>
<td>删除文件时会调用的钩子，一旦传入该函数，内部删除将不会执行。</td>
<td>Function，<code v-pre>(file, value) =&gt; {}</code></td>
<td>无</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>Upload Methods</h5>
<table>
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>onProgressPercent</td>
<td>使用者自定义上传时，可通过该方法实现上传进度</td>
<td>Function:(percent) =&gt; {}</td>
</tr>
</tbody>
</table>
<h5>Upload solt</h5>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>占位内容</td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      src: ""
    };
  },
  methods: {
    onSuccess(file) {
      this.src = URL.createObjectURL(file);
    }
  }
};
</script>


